<template>
  <div class="article-item">
    <van-cell :to="`/article/${article.art_id}`">
      <template #title>
        <!-- 文字省略 最多显示两行 -->
        <div class="article-title van-multi-ellipsis--l2">{{ article.title }}</div>
      </template>
      <!--插槽#label：3张图片盒子部分， 作者等发布信息 -->
      <template #label>
        <div v-if="article.cover.type === 3" class="cover-wrap">
          <div class="cover-item" v-for="(img, idx) in article.cover.images" :key="idx">
            <van-image class="right-image" fit="cover" :src="img">
            </van-image>
          </div>
        </div>
        <!-- 作者等发布信息 -->
        <div class="label-info-wrap">
          <span>{{ article.aut_name }}</span>
          <span>{{ article.comm_count }} 评论</span>
          <span>{{ article.pubdate | relativeTime }}</span>
        </div>
      </template>
      <!-- 1张图片盒子部分(默认右边) -->
      <template #default v-if="article.cover.type === 1">
        <van-image class="right-image" fit="cover" :src="article.cover.images[0]">
        </van-image>
      </template>
    </van-cell>
  </div>
</template>

<script>

export default {
  name: 'ArticleItem',
  props: {
    article: {
      type: Object,
      default: () => { }
    }
  },
  data () { return {} }
}
</script>
<style lang="less" scoped>
.article-item {
  .article-title {
    font-size: 32px;
    color: #3a3a3a;
  }

  .van-cell__value {
    flex: unset;
    width: 230px;
    height: 146px;
    padding-left: 15px;
  }

  .right-image {
    width: 100%;
    height: 100%;
  }

  .label-info-wrap span {
    font-size: 22px;
    color: #b4b4b4;
    margin-right: 20px;
  }

  .cover-wrap {
    display: flex;
    padding: 30px 0;

    .cover-item {
      height: 146px;
      flex: 1;

      &:not(:last-child) {
        // margin-right: 20px;
        padding-right: 4px;
      }
    }
  }
}
</style>
