<template>
  <div class="article-list">
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh" :success-text="refreshingText" :success-duration="1500">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" :error.sync="error"
        error-text="请求失败，点击重新加载" @load="onLoad">
        <ArticleItem v-for="(item, index) in list" :key="index" :article="item"></ArticleItem>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArticlesAPI } from '@/api/index'
import ArticleItem from './ArticleItem.vue'

export default {
  name: 'ArticleList',
  components: {
    ArticleItem
  },
  props: {
    channel: {
      type: Object,
      default: () => ({})
    }
  },
  data () {
    return {
      list: [], // 存储列表数据的数组
      loading: false, // 控制加载中 loading 状态
      finished: false, // 控制数据加载结束的状态
      timestamp: null, // 请求下一页数据的时间戳
      error: false,
      refreshing: false, // 下拉刷新
      refreshingText: '刷新成功'

    }
  },
  methods: {
    async onLoad () {
      try {
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id, // 频道 id
          timestamp: this.timestamp || Date.now() // 时间戳，请求新的推荐数据传当前的时间戳，请求历史推荐传指定的时间戳
        })
        // 2. 把数据添加到 list 数组中
        this.list.push(...res.data.results)
        // 3. 设置本次加载中 loading 状态结束
        this.loading = false
        // 4. 判断数据是否加载结束
        if (res.data.results.length) {
          // 更新获取下一页数据的时间戳
          this.timestamp = res.data.pre_timestamp
        } else {
          // 没有数据了，设置加载状态结束，不再触发上拉加载更多了
          this.finished = true
        }
      } catch (error) {
        this.loading = false
        // 展示错误提示状态
        this.error = true
        this.$toast('接口请求失败，稍后重试')
      }
    },

    async onRefresh () {
      try {
        const { data: res } = await getArticlesAPI({
          channel_id: this.channel.id, // 频道 id
          timestamp: Date.now() // 时间戳，请求新的推荐数据传当前的时间戳
        })
        this.list.unshift(...res.data.results)
        // 关闭下拉刷新状态
        this.refreshing = false
        // 下拉刷新提示内容
        this.refreshingText = `刷新成功,更新了${res.data.results.length}条记录`
      } catch (error) {
        this.refreshingText = '刷新失败，请稍后重试'
        this.refreshing = false
      }
    }
  }
}
</script>

<style scoped lang="less">
.article-list {
  // height: 100%; //设置100%无效，是相当于父元素高度的100%,但是父元素没有高度，
  height: 79vh; // 视口vw和vh（移动端布局）不受父元素影响，计算：（667-（46+41+50）/667 ≈ 0.79）
  // 1vw = 视口宽度的百分之一
  // 1vh = 视口高度的百分之一
  overflow-y: auto;
}
</style>
