<template>
  <div class="home-container">
    <!-- 导航栏 -->
    <van-nav-bar title="首页" class="page-nav-bar" fixed>
      <template #title>
        <van-button class="search-btn" type="info" round size="small" icon="search" to="/search">搜索</van-button>
      </template>
    </van-nav-bar>
    <!-- 导航栏 -->

    <!-- 频道列表 -->
    <!--
      通过v-model绑定当前激活标签对应的索引值，默认情况下启用第一个标签
      通过animated属性可以开启切换标签内容时的转场动画
      通过swipeable属性可以开启滑动切换标签页
      通过swipe-threshold属性设置最低滑动标签数
    -->
    <van-tabs v-model="active" animated swipeable swipe-threshold=5 class="channle-tabs">
      <van-tab v-for="item in channels" :key="item.id" :title="item.name">
        <ArticleList :channel="item"></ArticleList>
      </van-tab>

      <!-- 右侧icon -->
      <template #nav-right>
        <div class="placeholder"></div>
        <div class="hamburger-btn" @click="isChennelEditShow = true">
          <i class="iconfont icon-gengduo"></i>
        </div>
      </template>
    </van-tabs>
    <!-- 弹层 -->
    <van-popup class="edit-channel-popup" v-model="isChennelEditShow" position="bottom" :style="{ height: '90%' }"
      closeable>
      <ChannelEdit :myChannels="channels" :allChannels="allChannels" :active="active" @upActive="onActive" />
    </van-popup>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getItem } from '@/utils/storage'
import { getUserChannelsAPI, getAllChannelAPI } from '@/api/index'
import ArticleList from '@/components/Article/ArticleList.vue'
import ChannelEdit from '@/components/Home/ChannelEdit.vue'
export default {
  data () {
    return {
      active: 0, // 索引
      channels: [], // 频道列表
      allChannels: [], // 所有频道列表
      isChennelEditShow: false // 这里我们先设置为 true 就能看到弹窗的页面了
    }
  },
  components: {
    ArticleList,
    ChannelEdit
  },
  created () {
    // 调用获取频道列表
    this.loadChannels()
    this.loadAllChannels()
  },
  computed: {
    // 通过状态管理器中的user来判断用户是否登录
    ...mapState(['user'])
  },
  methods: {
    async loadChannels () {
      try {
        let tempChannels = []
        const localChannels = getItem('TOUTIAO_CHANNELS') // 未登陆 获取本地存储到数据
        if (this.user || !localChannels) {
          // 解构赋值拿到真实参数
          const { data: res } = await getUserChannelsAPI()
          tempChannels = res.data.channels
        } else {
          tempChannels = localChannels
        }

        this.channels = tempChannels
      } catch (err) {
        this.$toast('获取频道列表数据失败')
      }
    },
    async loadAllChannels () {
      try {
        const { data } = await getAllChannelAPI()
        this.allChannels = data.data.channels
      } catch (err) {
        this.$toast('获取所有频道列表失败')
      }
    },
    // 自定义组件事件(子传父，修改父组件中两个属性)
    onActive (index, isShow = false) {
      this.active = index // 修改索引
      this.isChennelEditShow = isShow // 关闭,设置默认值
    }
  }
}
</script>
<style lang="less" scoped>
// 搜索框宽度设置
:deep(.van-nav-bar__title) {
  max-width: unset;
}

.home-container {
  padding-top: 174px;
  padding-bottom: 100px;

  .search-btn {
    background-color: #5babfb;
    border: node;
    width: 555px;
    height: 64px;
    font-size: 28px;
  }

  :deep(.channle-tabs) {
    .van-tabs__wrap {
      height: 82px;
      position: fixed;
      top: 92px;
      left: 0;
      right: 0;
    }

    .van-tab {
      border-right: 1px solid #edeff3;
      min-width: 200px;
      font-size: 30px;
      color: #777;
    }

    .van-tab--active {
      color: #333;
      font-weight: 700;
    }

    .van-tabs__line {
      width: 32px;
      background-color: #5babfb;
    }
  }

  .placeholder {
    width: 66px;
    flex-shrink: 0;
  }

  .hamburger-btn {
    position: fixed;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 66px;
    height: 82px;
    background-color: rgba(255, 255, 255, 0.902);

    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 2px;
      height: 58px;
      background-image: url(../../assets/gradient-gray-line.png);
      background-size: contain;
    }
  }

  .edit-channel-popup {
    padding-top: 100px;
    box-sizing: border-box;
  }
}
</style>
