<template>
  <div class="channel-edit">
    <van-cell title="我的频道">
      <template #default>
        <van-button size="mini" round plain type="danger" class="edit-btn" @click="isShowEdit = !isShowEdit">
          {{ isShowEdit ? '完成' : '编辑' }}
        </van-button>
      </template>
    </van-cell>
    <van-grid :gutter="10" class="my-grid">
      <van-grid-item class="channel-item" v-for="(channel, index) in myChannels" :key="channel.id" :text="channel.name"
        :class="{ active: active === index }" @click="onChannel(channel, index)">
        <!-- icon=close 变成插槽,独立控制 -->
        <template #icon>
          <van-icon name="close" v-show="isShowEdit && index !== 0" />
        </template>
      </van-grid-item>

    </van-grid>
    <!-- 推荐频道 -->
    <van-cell title="推荐频道"></van-cell>
    <!-- direction="horizontal"  -->
    <van-grid :gutter="10" class="recommend-grid">
      <!-- 子组件内请求接口获取有延迟，从父组件传过来效果更好 -->
      <!-- <van-grid-item class="channel-item" v-for="channel in channelList" :key="channel.id" :text="channel.name" icon="plus" /> -->
      <!-- <van-grid-item class="channel-item" v-for="channel in allChannels" :key="channel.id" :text="channel.name" icon="plus" /> -->
      <!-- <van-grid-item class="channel-item" v-for="channel in othAllChannels" :key="channel.id" :text="channel.name" icon="plus" /> -->
      <van-grid-item class="channel-item" v-for="channel in othAllChannels_lodash" :key="channel.id"
        :text="channel.name" icon="plus" @click="onAddChannel(channel)" />
    </van-grid>
  </div>
</template>

<script>
import { getAllChannelAPI, addUserChannelAPI, deleteUserChannelAPI } from '@/api/index'
import { setItem } from '@/utils/storage'
import { mapState } from 'vuex'
export default {
  name: 'ChannelEdit',
  props: {
    myChannels: {
      type: Array,
      default: () => []
    },
    allChannels: { // 所有频道
      type: Array,
      default: () => []
    },
    active: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      isShowEdit: false,
      channelList: [],
      myChannelsCopy: this.myChannels
    }
  },
  computed: {
    // 通过状态管理器中的user来判断用户是否登录
    ...mapState(['user']),
    othAllChannels () {
      // 类似双重for循环
      // filter遍历数组，获取全部频道中每一个元素
      const othChannels = this.allChannels.filter(channel => {
        return !this.myChannels.find(obj => {
          return obj.id === channel.id
        })
      })
      console.log(othChannels)
      return othChannels
    },
    othAllChannels_lodash () {
      return this.$_.differenceBy(this.allChannels, this.myChannels, 'id')
    }
  },
  created () {
    this.getAllChannels()
  },
  methods: {
    async getAllChannels () {
      try {
        const { data: res } = await getAllChannelAPI()
        this.channelList = res.data.channels
      } catch (error) {
        console.log(error)
      }
    },
    // 添加频道元素，设计到改变父组件数据,先克隆原始数据
    // 数据持久化
    async onAddChannel (channel) {
      this.myChannelsCopy.push(channel)
      // 判断用户是否登录
      if (this.user) {
        await addUserChannelAPI({
          id: channel.id,
          seq: this.myChannels.length - 1
        })
        this.$toast('添加成功')
      } else {
        // 未登陆 把数据存储到本地
        setItem('TOUTIAO_CHANNELS', this.myChannels)
      }
    },
    // 操作我的频道（调整或删除）
    onChannel (channel, index) {
      if (this.isShowEdit) {
        // 删除
        this.myChannelsCopy.splice(index, 1)
        if (index <= this.active) {
          // 删除元素且不关闭弹层
          this.$emit('upActive', this.active - 1, true)
        }
        this.deleteChannel(channel)
      } else {
        // 跳转
        this.$emit('upActive', index)
      }
    },
    // 删除数据持久化
    async deleteChannel (channel) {
      try {
        if (this.user) {
          // 已登录，将数据存储到线上
          await deleteUserChannelAPI(channel.id)
        } else {
          // 未登录，将数据存储到本地
          setItem('channles', this.myChannels)
        }
      } catch (err) {
        console.log(err)
        this.$toast('删除频道失败，请稍后重试')
      }
    }
  }
}
</script>

<style scoped lang="less">
.channel-edit {
  .van-cell__title {
    height: 58px;
    line-height: 58px;
    font-size: 32px;
    color: #333
  }

  .edit-btn {
    width: 100px;
    height: 48px;
    line-height: 48px;
    color: #f85959;
    border: 1px solid #f85959;
  }

  :deep(.my-grid) {

    .van-grid-item__content {
      white-space: nowrap;
      flex-direction: row;

      // 右上角绝对定位
      .van-grid-item__icon-wrapper {
        position: absolute;
        top: -10px;
        right: -10px;
        color: #cacaca;
        font-size: 28px;
        z-index: 2;
      }

      .van-grid-item__text {
        color: #222;
        font-size: 28px;
        margin-top: 0;
      }
    }

    // 激活选中样式
    .active {
      .van-grid-item__text {
        color: #f85959;
      }
    }
  }

  .recommend-grid {
    :deep(.van-grid-item__content) {
      // 使用direction="horizontal" 将 direction 属性设置为 horizontal，可以让宫格的内容呈横向排列。
      white-space: nowrap;
      flex-direction: row;

      .van-icon-plus {
        color: #222;
        font-size: 28px;
        margin-right: 6px;
      }

      .van-grid-item__text {
        color: #222;
        font-size: 28px;
        margin-top: 0;
      }
    }
  }

  .channel-item {
    height: 86px;
    width: 160px;

    :deep(.van-grid-item__content) {
      background-color: #f5f6f6;
      // 会覆盖上面active样式
      // .van-grid-item__text {
      //   color: #222;
      //   font-size: 28px;
      //   margin-top: 0;
      // }
    }
  }
}
</style>
